import React from 'react'

import classes from './P.module.css'
import css from '../Styles/text'

const P = (props) => {
  var styles = css(props)
  return (
    <>
      <div style={{ textAlign: props.align || 'center' }}>
        <p style={styles}>
          {props.title} &nbsp;&nbsp;:&nbsp;&nbsp;
          {props.children}
        </p>
      </div>
    </>
  )
}

export default P
