import React from 'react'

import classes from './H.module.css'
import css from '../Styles/text'

const H = (props) => {
  var styles = css(props)
  return (
    <>
      <div style={{textAlign:props.align || 'center'}}>
        <h1 style={styles}>
          {props.title}
          {props.children}
        </h1>
      </div>
    </>
  )
}

export default H
