import React, { useState, useEffect } from 'react'

import axios from './axios'

import { H, P } from './UI'

function App() {
  const [addNames, setAddNames] = useState(['Ajay', 'Thejus'])
  const [defaultName, setDefaultName] = useState(addNames[1])
  const [form, setForm] = useState([])
  const [updateData, setupdateData] = useState(false)

  useEffect(() => {
    axios
      .get('/chat.json')
      .then((response) => {
        var allData = []
        for (let i in response.data) {
          allData.push(response.data[i])
        }
        setForm(allData)
      })
      .catch((err) => console.log(err))
  }, [updateData])


  const addNewPerson = (event) => {
    event.preventDefault()
    var newName = event.target.addPerson.value
    setAddNames([...addNames, newName])
    event.target.addPerson.value = ''
    setDefaultName(newName)
  }

  const submittingForm = (event) => {
    event.preventDefault()
    var data = {
      id: form.length + 1,
      name: event.target.name.value,
      content: event.target.content.value,
    }
    axios
      .post('/chat.json', data)
      .then((response) => {
        setupdateData(!updateData)
      })
      .catch((err) => console.log(err))
    event.target.content.value = ''
  }

  const handleSelectValue = (event) => {
    event.preventDefault()
    setDefaultName(event.target.name.value)
  }

  return (
    <>
      <H title='Conversation' size='4' mt='1' color='22c' mb='2'></H>

      <center>
        <div style={{ width: '15rem' }}>
          <form method='post' onSubmit={addNewPerson}>
            <input
              type='text'
              className='form-control'
              placeholder='Add New Name'
              name='addPerson'
            />
            <button type='submit' className='btn btn-primary mt-4 mb-4'>
              Add
            </button>
          </form>
        </div>
      </center>

      <center>
        <div style={{ width: '40rem' }}>
          <form method='post' onSubmit={submittingForm}>
            <div className='form-row'>
              <div className='col-3'>
                <select
                  className='form-control'
                  name='name'
                  value={defaultName}
                  onChange={handleSelectValue}
                >
                  {addNames.map((single) => {
                    return (
                      <option key={single} value={single}>
                        {single}
                      </option>
                    )
                  })}
                </select>
              </div>
              <div className='col'>
                <input
                  type='text'
                  className='form-control'
                  placeholder='Enter Content'
                  name='content'
                />
              </div>
            </div>
            <button type='submit' className='btn btn-primary mt-4 btn-lg'>
              Save
            </button>
          </form>
        </div>
      </center>
      {form.map((data) => {
          return (<P
            key={data.id}
            title={data.name}
            size='1.5'
            color='22c'
            align='left'
            ml='2'
            mt='0.5'
          >
            {data.content}
          </P>)
      })}
    </>
  )
}

export default App
