const styles = (props) => {
  var ruling = props.rule || 'rem'
  return {
    fontFamily: 'Dancing Script, cursive',
    fontSize: props.size + ruling,
    marginTop: props.mt + ruling,
    marginLeft: props.ml + ruling,
    marginBottom: props.mb + ruling,
    marginRight: props.mr + ruling,
    paddingTop: props.pt + ruling,
    paddingLeft: props.pl + ruling,
    paddingBottom: props.pb + ruling,
    paddingRight: props.pr + ruling,
    color: '#' + props.color,
  }
}

export default styles
